import { $post, proxy, $get } from './WebJsFunc';
export var getHupuWS = function (callback) {
    callback('tcp.lb.liangle.com:3081');
};
var _proxyJSON = function (url) { return proxy(url, 'json'); };
export function setClientDelay(gameId, sec, callback) {
    var url = "http://pre.liangle.com/api/pbk/event/delay/" + gameId;
    var data = { ':game_id': gameId + "", ctd: sec + '' };
    console.log(setClientDelay, data);
    $post(_proxyJSON(url), data, callback);
}
export function getClientDelay(gameId, callback) {
    var url = "http://pre.liangle.com/api/pbk/event/delay/" + gameId;
    _get(_proxyJSON(url), callback);
}
export function getPreRoundPlayer(gameId, callback) {
    var url = 'http://api.liangle.com/api/passerbyking/game/wheel/ready/' + gameId;
    _get(_proxyJSON(url), callback);
}
export function getAllPlayer(gameId, callback) {
    var url = 'http://api.liangle.com/api/passerbyking/game/players/' + gameId;
    _get(_proxyJSON(url), callback);
}
export function getRoundList(callback) {
    var url = 'http://api.liangle.com/api/passerbyking/game/list';
    _get(_proxyJSON(url), callback);
}
export function getRoundRawDate(gameId, callback) {
    var url = 'http://api.liangle.com/api/passerbyking/game/match/' + gameId;
    _get(_proxyJSON(url), callback);
}
var _get = function (url, callback) {
    $get(url, callback);
};
export var getPlayerDoc = function (callback) {
    $get('/game/player', function (res) {
        callback(res);
    });
};
export var updatePlayerDoc = function (playerDoc, callback) {
    $post('/game/player/update', playerDoc, callback);
};
export var getGameInfo = function (callback) {
    _get('/game/', callback);
};
export var _avatar = function (filename) {
    return '/img/player/avatar/' + filename;
};



